.footer {
  background-color: $background-color__ivory;
  padding-top: 113px;
}

.footer__title {
  margin-bottom: 43px;
  padding-right: 51px;
  margin-left: 5px;
  display: flex;
  flex-direction: column
}

.footer__title-one {
  display: inline-block;
  font-family: Titan One;
  margin-bottom: 17px;

  font-size: 18px;
  line-height: 1.17;
  text-transform: uppercase;
  color: $main-color__pale-pink;
}

.footer_title-two {
  display: inline-block;
  font-family: Titan One;
  font-size: 34px;
  line-height: 1.15;
  letter-spacing: 0.04em;
  text-transform: uppercase;

  color: $main-color__bright-pink;
}

.footer-icons__block {
  display: flex;
  justify-content: flex-end;
  margin-right: 5px;
  margin-bottom: 46px;
}

.footer-icons__item:not(:last-child) {
  margin-right: 28.24px;
}

.footer-icons {
  fill: $icons-color__pink;

  &:focus,
  &:hover {
    fill: #D41443;
  }
}

.footer__contact {
  display: block;
  font-family: DM Sans;
  font-weight: bold;
  font-size: 12px;
  line-height: 1.86;
  text-align: right;

  color: $main-color__black;

  margin-bottom: 43px;

}

.footer__phone {
  color: $main-color__bright-pink;
}

.footer__rights {
  position: relative;
  background-color: $background-color__ivory;
  text-align: center;
  margin: 0px -15px 0 -15px;
  padding: 22px 91px 25px 79px;

  font-weight: bold;
  font-size: 14px;
  line-height: 1.52;
  color: $main-color__grey;
  border-top: 1px solid #E1E1E1;
}

@media screen and (min-width: 768px) {
  .footer {
    padding-top: 158px;

  }

  .footer__contacts {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    padding-left: 16px;
    padding-right: 16px;
    padding-bottom: 91px;
  }

  .footer__title {
    /*  width: 340px; */
    margin-right: 92px;
    padding: 0;
    margin-bottom: 0;
  }

  .footer__title-one {
    font-size: 26px;
    line-height: 1.15;
    letter-spacing: 0.04em;
  }

  .footer_title-two {
    font-size: 48px;
    line-height: 1.14;
    letter-spacing: 0.06em;

  }

  .footer-icons {
    width: 34px;
    height: 34px;
  }

  .footer__contact {
    font-size: 14px;
    white-space: nowrap;
  }

  .footer__rights {
    padding-top: 29px;
    padding-bottom: 18px;
    letter-spacing: 0.02em;
  }
}

@media screen and (min-width: 1200px) {
  .footer {
    padding-top: 165px;
    width: 100%;
  }

  .footer__contacts {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    padding-left: 114px;
    padding-right: 116px;
    padding-bottom: 97px;
  }

  .footer__title-one {
    font-size: 30px;
    line-height: 1.13px;
    margin-bottom: 15px;
  }

  .footer_title-two {
    font-size: 58px;
    line-height: 1.14;

  }

  .footer-icons__block {
    margin-bottom: 50px;
  }

  .footer__contact {
    font-size: 16px;
  }

  .footer__rights {
    padding-top: 9px;
    padding-bottom: 10px;

  }
}